import React from 'react';

const CommentsList = ({ comments }) => {
  if (!comments) {
    return (
      <React.Fragment>
        <h3>Comments</h3>
        <p>No Comments</p>
      </React.Fragment>
    );
  }

  return (
    <React.Fragment>
      <h3>Comments</h3>

      {comments.map((comment, index) => {
        return (
          <div className='comment' key={index}>
            <h4>{comment.username}</h4>
            <p>{comment.text}</p>
          </div>
        );
      })}
    </React.Fragment>
  );
};

export default CommentsList;
