import React, { useState } from 'react';

const AddCommentForm = ({ name, setArticleInfo }) => {
  //initial values of our username and comment
  const [username, setUsername] = useState('');
  const [commentText, setCommentText] = useState('');

  const addComment = async () => {
    const result = await fetch(`/api/articles/${name}/add-comment`, {
      method: 'post',
      //we do this when sening extra information in the post method
      body: JSON.stringify({ username, text: commentText }),
      //we add a headr when we are sending a post request withthe JSON body
      headers: {
        'Content-Type': 'application/json',
      },
    });

    const response = await result.json();
    // console.log(response);
    setArticleInfo(response); // we do this to update the UI

    setUsername('');
    setCommentText('');
  };

  return (
    <div id='add-comment-form'>
      <h3>Add a comment</h3>
      <input
        placeholder='Enter Username'
        value={username}
        onChange={(e) => setUsername(e.target.value)}
      ></input>
      <textarea
        rows='4'
        cols='50'
        placeholder='Enter Comment'
        value={commentText}
        onChange={(e) => setCommentText(e.target.value)}
      ></textarea>
      <button
        onClick={() => {
          addComment();
        }}
      >
        Add Comment
      </button>
    </div>
  );
};

export default AddCommentForm;
