import React, { useState, useEffect } from 'react';
import AddCommentForm from '../components/AddCommentForm';
import ArticlesList from '../components/ArticlesList';
import CommentsList from '../components/CommentsList';
import UpvoteSection from '../components/UpvoteSection';
import articleContent from './article-content';
import NotFoundPage from './NotFoundPage';

const ArticlePage = ({ match }) => {
  const name = match.params.name;
  const article = articleContent.find((article) => article.name === name);

  const [articleInfo, setArticleInfo] = useState({ upvotes: 0, comments: [] });

  useEffect(() => {
    const fetchData = async () => {
      const result = await fetch(`/api/articles/${name}`);

      const response = await result.json();
      // console.log(response);

      setArticleInfo(response);
    };
    fetchData();
  }, [name]);

  //If the artcle does not exist
  if (!article) {
    return <NotFoundPage />;
  }

  //get all other articles and filter our the current article
  const relatedArticles = articleContent.filter(
    (article) => article.name !== name
  );

  return (
    <React.Fragment>
      <h1>{article.title}</h1>

      {article.content.map((paragraph, index) => {
        return <p key={index}>{paragraph}</p>;
      })}
      <div className='upvotes-box'>
        <UpvoteSection
          articleName={name}
          upvotes={articleInfo.upvotes}
          setArticleInfo={setArticleInfo}
        />
      </div>
      <div className='comment-box'>
        <CommentsList comments={articleInfo.comments} />
        <AddCommentForm name={name} setArticleInfo={setArticleInfo} />
      </div>

      <div className='related-articles--section'>
        <h5>Related Articles</h5>
        <div className='related-articles'>
          <ArticlesList articles={relatedArticles} />
        </div>
      </div>
    </React.Fragment>
  );
};

export default ArticlePage;
